<template>
    <div class="search">
        <input  v-model="mz1" placeholder="请输入歌手或者歌名">
        <button style="background-color: blueviolet;" @click="sou1">搜索</button>
      <ul class="geshou">
          <li v-for="item1 in mz">{{item1}}</li>
      </ul>
        <ul class="gequ">
            <li v-for="item in gm" @click="tiao(item.id)" :key="item.id">
              {{item.name}}
              <p class="miaoshu">{{item.album.name}}</p></li>
        </ul>
    </div>
</template>
<script>
import axios from '../axios/instance.js'
export default {
    name:'sou1',
    data(){
        return {
            mz1:'',
            mz:[],
            gm:[]
        }
    },
    created() {
        this.sou()
    },
    methods: {
        sou(){
            axios({
        url: "/search?keywords= " + this.$route.query.name1,  /*热门歌单接口地址*/
        method: 'post'
      })
        .then(res => {
          console.log("我拿到的数据：", res.data.result.songs)
          this.gm=res.data.result.songs
        })
        .catch(err => {
          console.log(err)
        })
        },
        sou1(){
         this.mz.push(this.mz1)
         axios({
        url: "/search?keywords= " + this.mz1,  /*热门歌单接口地址*/
        method: 'post'
      })
        .then(res => {
          console.log("我拿到的数据：", res.data.result.songs)
          this.gm=res.data.result.songs
        })
        .catch(err => {
          console.log(err)
        })
        },
        tiao(item){
   this.$router.push({
         path:'/geci?idd='+item
   })
        }
    },
}
</script>

<style  scoped>
  *{
        margin: 0px;
       padding: 0px;
    }
.search{
  margin: 0 auto;
       padding: 0px;
           text-align: center;
           height: 1340px;
           width: 100%;     
}
ul{
  list-style: none;
}
.search input{
         border-radius: 7px;
       background: fixed;
       outline: none;
       border:1px solid white;
       height: 38px;
       width: 70%;
       text-indent: 2em;
      color: purple;
       font-size: 25px;
      margin-top: 25px;
     }
      input::-webkit-input-placeholder{
            color:white;
            font-size: 12px;
        }
        .search  button{
         color: white;
          height: 40px;
           width: 20%;
           background-color: blueviolet;
           border: none;
           margin-left: 15px;
    border-radius: 7px;
           background: fixed;
      }
      .search .gequ li{
  font-size: 25px;
  width: 100%;
  padding: 20px 0px;
  background: white;
  opacity: 0.5;
   text-align: left;
   text-indent: 0.7em;
  margin: 10px 0px;
}
.geshou li{
  float: left;
  margin-top: 6px;
  padding: 6px 7px;
  margin-left: 5px;
  background-color:orange;
  border-radius: 4px;
}
.gequ li{
  float: right;
}
.miaoshu{
  margin-top: 10px;
  font-size: 18px;
}
</style>