<template>
  <div class="geci">
    <div class="music">
      <div class="singname">
        <p>{{names}}</p>
      </div>
      <div class="sing-pic">
        <img :src="tu" />
      </div>
    </div>
    <div class="mm">
      <marquee
        direction="up"
        loop="-1"
        behavior="scroll"
        scrollDelay="5"
        height="300"
        scrollamount="4"
        onmouseout="start();"
        onmouseover="stop();"
        style=" white-space: pre-wrap;text-align: center;"
      >{{kg}}</marquee>
    </div>
  </div>
</template>
<script>
import axios from "../axios/instance.js";
export default {
  name: "geci",
  data() {
    return {
      kg: "",
      gm: [],
      mz: [],
      tu: "",
      gename: "",
      names: "",
      singer: ""
    };
  },
  methods: {
    geci() {
      axios({
        url: "/lyric?id= " + this.$route.query.idd /*热门歌单接口地址*/,
        method: "post"
      })
        .then(res => {
          this.kg = res.data.lrc.lyric;
        })
        .catch(err => {
          console.log(err);
        });
    },
    img() {
      axios({
        url: "/song/detail?ids= " + this.$route.query.idd /*热门歌单接口地址*/,
        method: "post"
      })
        .then(res => {
      
          this.tu = res.data.songs[0].al.picUrl;
          this.names = res.data.songs[0].name;
              console.log(res.data.songs[0].al.picUrl);
          console.log(this.tu);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    this.geci();
    this.img();
  }
};
</script>
<style  scoped>
* {
  margin: 0;
  padding: 0;
}

.music {
  width: 100%;
  height: 20em;
}
.singname {
  margin-bottom: 30px;
}
.singname p {
  text-align: center;
  color: goldenrod;
  font-size: 20px;
  padding-top: 16px;
}
.sing-pic {
  width: 50%;
  height: 70%;
  margin: 0px auto;
  /* border: 1px solid red; */
  background-position: center;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: contain;
}
.sing-pic img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  margin-bottom: 20px;
  animation: rotate 30s linear 0s infinite normal both running;
}
@keyframes rotate {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}

.mm {
  width: 100%;
  height: 30em;
  overflow: hidden;
  white-space: pre-wrap;
}
</style>